'use client';
import dynamic from 'next/dynamic';
import { useRef, useEffect, useState } from 'react';
import AivePulse from './AivePulse';
import AiveCore from './AiveCore';

// ‚úÖ Prevents SSR issues with 3D rendering
const Globe = dynamic(() => import('react-globe.gl'), { ssr: false });

export default function GlobeBackground({ activeCountry }: { activeCountry?: string }) {
  const globeRef = useRef<any>(null);
  const [pulseActive, setPulseActive] = useState(false);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  // üéµ Initialize soft ambient hum
  useEffect(() => {
    const audio = new Audio('/sounds/aive_hum.mp3');
    audio.loop = false;
    audio.volume = 0.15;
    audioRef.current = audio;
  }, []);

  // üåê Set up the globe controls
  useEffect(() => {
    if (globeRef.current) {
      const controls = globeRef.current.controls();
      if (controls) {
        controls.autoRotate = true;
        controls.autoRotateSpeed = 0.6;
      }
      globeRef.current.pointOfView({ lat: 20, lng: 0, altitude: 2.3 });
    }
  }, []);

  // üåç Auto-zoom, glow, hum, and A.I.V.E. feedback
  useEffect(() => {
    if (!activeCountry || !globeRef.current) return;

    const countryCoordinates: Record<string, { lat: number; lng: number }> = {
      USA: { lat: 38, lng: -97 },
      Canada: { lat: 56, lng: -106 },
      Russia: { lat: 61, lng: 105 },
      China: { lat: 35, lng: 104 },
      Japan: { lat: 36, lng: 138 },
      India: { lat: 20, lng: 77 },
      Germany: { lat: 51, lng: 10 },
      UK: { lat: 55, lng: -3 },
      France: { lat: 46, lng: 2 },
      Brazil: { lat: -10, lng: -55 },
      Australia: { lat: -25, lng: 133 },
      Global: { lat: 20, lng: 0 },
    };

    const coords = countryCoordinates[activeCountry] || countryCoordinates.Global;

    // üåÄ Smooth zoom + rotation
    globeRef.current.pointOfView({ lat: coords.lat, lng: coords.lng, altitude: 1.5 }, 2000);

    // üí° Brief cyan glow on globe mesh
    const scene = globeRef.current.scene();
    const globeMesh = scene?.children?.find(
      (obj: any) => obj.type === 'Mesh' && obj.name.includes('globe')
    );

    if (globeMesh && globeMesh.material) {
      const original = globeMesh.material.color.clone();
      globeMesh.material.color.set('#38bdf8');
      setTimeout(() => globeMesh.material.color.copy(original), 1500);
    }

    // üó£Ô∏è A.I.V.E. speaks and activates pulse
    const message = `Accessing live intelligence for ${activeCountry}`;
    const synth = window.speechSynthesis;
    if (synth) {
      const utter = new SpeechSynthesisUtterance(message);
      utter.pitch = 1;
      utter.rate = 0.9;
      utter.volume = 0.8;
      utter.lang = 'en-US';
      synth.cancel();
      synth.speak(utter);

      // ü©µ Trigger pulse and hum
      setPulseActive(true);
      if (audioRef.current) {
        audioRef.current.currentTime = 0;
        audioRef.current.play().catch(() => {});
      }
    }
  }, [activeCountry]);

  return (
    <div className="absolute inset-0 -z-10 opacity-70">
      {/* ü©µ A.I.V.E. Neural Core & Halo */}
      <AiveCore trigger={pulseActive} />

      {/* üåä A.I.V.E. Pulse Aura */}
      <AivePulse trigger={pulseActive} />

      {/* üåê Rotating 3D Globe */}
      <Globe
        ref={globeRef}
        globeImageUrl="//unpkg.com/three-globe/example/img/earth-night.jpg"
        bumpImageUrl="//unpkg.com/three-globe/example/img/earth-topology.png"
        backgroundColor="rgba(2,6,23,1)"
        showAtmosphere={true}
        atmosphereColor="#38bdf8"
        atmosphereAltitude={0.25}
      />
    </div>
  );
}
